        text-decoration-line: underline;
        color: #273043;
        border-bottom: 1px solid #CECDCD;
        padding-bottom: 1.2rem;
        display: flex;
    }

    .accordion img {
        align-self: flex-end;
        margin-left: 1rem;
        margin-bottom: .3rem
    }

    /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
    .active,
    .accordion:hover {
        opacity: 0.7;
    }

    /* Style the accordion panel. Note: hidden by default */
    .panel {
        padding: 0 18px;
        background-color: white;
        display: none;
        overflow: hidden;
    }

    .showPanel {
        padding: 0 18px;
        background-color: white;
        overflow: hidden;
    }

    .faq-section {
        height: fit-content;
    }

    #faq {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5%;
    }

    #faq h2 {
        align-self: flex-start;
        margin-left: 20%;
        margin-bottom: 10%;
    }

    #faq div {
        align-self: center;
    }



    /* Journal */

    .journal-section {
        height: fit-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        padding-top: 0;
    }

    .journal-section p {
        width: 156px;
        height: 22px;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 21px;
        letter-spacing: 0.4px;
        color: #273043;
        margin-top: 0px;
    }

    .journal-section>button {
        align-self: flex-start;
        margin: 60px 0px 86px 150px
    }

    .journal-section>h2 {
        align-self: flex-start;
        margin: 91px 0px 0px 100px
    }

    .journal-item {
        padding-top: 10px;
        margin: 10px;
        height: fit-content;
        display: flex;
        flex-direction: column;
    }

    .journal-item h4 {
        margin: 1rem 1rem;
        margin-left: 0em;
        padding-bottom: 0;
        height: fit-content;
    }

    .journal-copy-copy img {
        width: 90%
    }

    #journal-copy {
        width: 100%;
        height: fit-content;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    /* Mentions */

    .mentioned-in {
        background-color: #FFFEFE;
        display: grid;
        grid-template-columns: 20% 1fr;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: fit-content;
        padding: 70px 0px;
    }

    .mentioned-in h4 {
        height: fit-content;
        text-decoration: underline;
    }

    .mentioned-in-logos {
        display: flex;
        height: fit-content;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;
    }
}